import useStyles from 'isomorphic-style-loader/useStyles';
import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import s from './Section.css';

export default function Section({ title, html, tickets }) {
  let loaded = true;
  useStyles(s);
  useEffect(() => {
    if (tickets) {
      loaded = false;
      const script = document.createElement('script');
      script.src =
        '//www.travelpayouts.com/widgets/1aebd06a60eeaf6462c40863fd5d1c20.js?v=2105';
      script.async = true;
      const el = document.getElementById('aviasales');
      el.appendChild(script);
      loaded = true;
      return () => el.removeChild(script);
    }

    return () => {};
  });
  if (loaded) {
    return (
      <div className={s.root}>
        <div className={s.container} id={tickets ? 'aviasales' : 'text'}>
          <div dangerouslySetInnerHTML={{ __html: title }} />
          <div dangerouslySetInnerHTML={{ __html: html }} />
        </div>
      </div>
    );
  }
}

Section.propTypes = {
  title: PropTypes.string.isRequired,
  html: PropTypes.string.isRequired,
  tickets: PropTypes.bool,
};

Section.defaultProps = {
  tickets: false,
};
